.slider {
  &--home-page {
    margin: 56px 0 90px;

    @media (max-width: 1200px) {
      margin: 32px 24px 74px;
    }

    @media (max-width: 640px) {
      margin: 24px 0 56px;
    }
  }

  &__container {
    margin: 0 auto;
    max-width: 1136px;
    display: grid;
    max-height: 400px;
    grid-template-columns: 32px 1fr 32px;
    justify-content: center;
    gap: 16px;

    @media (max-width: 640px) {
      grid-template-columns: 1fr;
      max-width: 640px;
      max-height: 640px;
    }
  }

  &__photo {
    width: 100%;
    max-height: 400px;

    @media (max-width: 640px) {
      max-height: unset;
    }
  }

  &__img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 8px;
    display: none;
    opacity: 0;

    @media (max-width: 640px) {
      border-radius: 0;
    }

    &--active {
      display: block;
      animation: fadeIn 0.3s ease-in;
      opacity: 1;
    }
  }

  &__swipe {
    border: 1px solid #B4BDC4;
    border-radius: 48px;
    background-color: #fff;
    cursor: pointer;

    @media (max-width: 640px) {
      display: none;
    }

    &:hover {
      border: 1px solid #0F0F11;
    }
  }

  &__select {
    display: flex;
    justify-content: center;
    gap: 14px;
  }

  &__option {
    width: 14px;
    height: 4px;
    background-color: #E2E6E9;
    margin-top: 18px;

    &--active {
      background-color: #0F0F11;
    }
  }
}

@keyframes fadeIn {
  from { opacity: 0; }
    to { opacity: 1; }
}
