.cart {
  flex-grow: 1;
  padding: 25px 16px 56px;
  max-width: 1200px;

  @media (min-width: 640px) {
    padding: 41px 24px 64px;
  }

  @media (min-width: 1200px) {
    padding: 41px 32px 80px;
  }

  &__container {
    display: flex;
    justify-content: center;
  }

  @media (min-width:640px) {
    padding-bottom: 64px;
  }

  @media (min-width:1200px) {
    padding-bottom: 80px;
  }

  &__content {
    @media (min-width: 1200px) {
      display: flex;
      gap: 16px;
    }
  }

  &__items {
    flex-grow: 2;
    margin-bottom: 32px;
  }

  &__title {
    font-weight: 800;
    font-size: 32px;
    color: #0F0F11;
    margin-bottom: 32px;
  }

  &__item {
    margin-bottom: 16px;
    padding: 16px;

    border: 1px solid #E2E6E9;
    border-radius: 16px;

    @media (min-width: 640px) {
      display: flex;
      justify-content: space-between;
    }

    &:last-child {
      margin-bottom: 0;
    }

    &__title {
      display: flex;
      align-items: center;
      margin-bottom: 23px;

      @media (min-width: 640px) {
        margin-bottom: 0;
      }

      &__image {
        height: 66px;
        width: 66px;
      }

      &__button {
        margin-right: 23px;
        height: 16px;
        width: 16px;
        border: none;
        background-image: url(../img/cross.svg);
        background-position: center;
        background-color: #FFFFFF;
        cursor: pointer;
      }

      &__text {
        font-weight: 600;
        font-size: 14px;
        color: #0F0F11;
      }
    }

    &__price {
      display: flex;
      justify-content: space-between;
      align-items: center;

      @media (min-width: 640px) {
        width: 220px;
      }

      &__text {
        font-weight: 800;
        font-size: 22px;
        color: #0F0F11;
      }

      &__quantity {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 96px;

        @media (min-width: 640px) {
          margin-right: 53px;
        }

        &__button {
          height: 32px;
          width: 32px;
          border-radius: 48px;
          background-color: white;
          cursor: pointer;
          border: 1px solid #B4BDC4;

          &:hover {
            border-color: #0F0F11;
          }

          &-isDisabled {
            border-color: #E2E6E9;
            color: #B4BDC4;
            cursor: default;

            &:hover {
              border-color: #E2E6E9;
            }
          }
        }
      }
    }
  }

  &__back {
    display: flex;
    gap: 10px;

    margin-bottom: 24px;

    &__button {
      cursor: pointer;
      background-image: url('../img/left-arrow-active.svg');
      background-repeat: no-repeat;
      background-position: center;
      border: none;
      background-color: #FFFFFF;
    }

    &__text {
      text-decoration: none;
      cursor: pointer;
      color: #89939A;
      font-weight: 700;
      font-size: 12px;
    }
  }

  &__total {
    flex-grow: 1;
    padding: 24px;
    border: 1px solid #E2E6E9;
    border-radius: 16px;

    &__price {
      height: 41px;
      text-align: center;
      color: #0F0F11;
      font-weight: 800;
      font-size: 32px;
    }

    &__text {
      margin-bottom: 16px;
      height: 21px;
      text-align: center;
      font-weight: 600;
      font-size: 14px;
      color: #89939A;
    }

    &__line {
      margin-bottom: 16px;

      content: '';

      border: 1px solid #E2E6E9;
    }

    &__button {
      cursor: pointer;

      height: 48px;
      width: 100%;

      color: #FFFFFF;

      border: none;
      border-radius: 8px;

      font-weight: 700;
      font-size: 14px;

      background-color: #216CFF;
    }
  }
}
